import  { Component } from "react";
import YouTube from "react-youtube";
import "./Youtube.css";

// https://youtu.be/_nBlN9yp9R8
// https://www.youtube.com/watch?v=_nBlN9yp9R8

class ReactYoutubeExample extends Component {
  videoOnReady(event) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
    console.log(event.target.pauseVideo)
  }
  render() {
    const opts = {
      height: "450",
      width: "95%",
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        autoplay: 0,
      },
    };
    const {videoId} = this.props
    return (
      <div className="movie__title">
        <center>
          <YouTube
            className="video__screen"
            videoId={videoId}
            opts={opts}
            onReady={this.videoOnReady}
          />
          <h6 className="movie__name">Movie Trailer</h6>
        </center>
      </div>
    );
  }
}

export default ReactYoutubeExample;
